// app/dashboard/wallet/page.tsx
'use client';

import { useEffect, useState } from 'react';
import { apiFetch, isAuthenticated } from '@/src/lib/api/api-client';

export default function WalletPage() {
  const [wallets, setWallets] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (!isAuthenticated()) return;
    (async () => {
      try {
        const res = await apiFetch('/wallets');
        setWallets(Array.isArray(res?.wallets) ? res.wallets : []);
      } catch (e: any) {
        setError(e?.message || 'Failed to load wallets');
      } finally {
        setLoading(false);
      }
    })();
  }, []);

  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-semibold">My Wallets</h1>
      <p className="text-muted-foreground">View balances across currencies.</p>

      {loading && (
        <div className="bg-card text-card-foreground rounded-xl border p-6">Loading wallets...</div>
      )}

      {error && (
        <div className="bg-red-50 dark:bg-red-900/20 text-red-700 dark:text-red-300 border border-red-200 dark:border-red-800 rounded-xl p-4">{error}</div>
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {wallets.map((w) => {
          const bal = parseFloat(String(w.available_balance ?? w.balance ?? 0));
          return (
            <div key={String(w.id)} className="bg-card text-card-foreground rounded-xl shadow-sm border p-6">
              <div className="flex items-center justify-between mb-4">
                <div>
                  <div className="text-sm text-muted-foreground">Currency</div>
                  <div className="text-xl font-semibold">{String(w.currency).toUpperCase()}</div>
                </div>
                <span className="px-2 py-1 rounded-md text-xs bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-300">Active</span>
              </div>
              <div className="text-sm text-muted-foreground">Available Balance</div>
              <div className="text-2xl font-bold mt-1">{bal.toLocaleString(undefined, { maximumFractionDigits: 2 })}</div>
              <div className="text-xs text-muted-foreground mt-4">ID: {String(w.id)}</div>
            </div>
          );
        })}
      </div>
    </div>
  );
}